import { PureComponent } from 'react';
import { Tile } from '@Green-Dot-Corporation/eureka-ui-layouts';

import ToDoList from './components/todo/ToDoList';
import ToDoListHeader from './components/todo/ToDoListHeader';
import ToDoService from './services/ToDoService';

// Styles
import './styles/ui-module.scss';

export default class ToDoModule extends PureComponent {
    state = {};

    render() {
        const { todos } = this.state;

        return (
            <Tile
                headingLvl={1}
                headingText="Things To DO"
                contextCls="module-temlpate"
            >
                <ToDoListHeader />
                <ToDoList todos={todos} />
            </Tile>
        );
    }

    async componentDidMount() {
        const todos = await this.fetchTODOList();
        this.setState({ todos });
    }

    fetchTODOList = async () => {
        const todos = await ToDoService.getTodos('/demo/todos');

        return todos;
    };
}
